import { useState } from "react";
import ReactMarkdown from "react-markdown";
import { app_background } from "./data/markdown";

import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Form from "react-bootstrap/Form";

import { Task } from "./components";

const App = () => {
  const [tasks, setTasks] = useState([]);
  const [currTask, setCurrTask] = useState("");

  const handleInput = (event) => {
    event.preventDefault();
    setCurrTask(event.target.value);
  };

  const addTask = () => setTasks([...tasks, currTask]);

  return (
    <div className="home">
      <div id="content">
        <ReactMarkdown source={app_background} />
        <Form>
          <Form.Group controlId="form-input">
            <FormControl placeholder="Add a task!" onChange={handleInput} />
            <InputGroup.Append>
              <Button onClick={addTask}>Add</Button>
            </InputGroup.Append>
          </Form.Group>
        </Form>
        {tasks.map((task) => (
          <div>
            <Task name={task} />
          </div>
        ))}
      </div>
    </div>
  );
};

export default App;
